import React from 'react';
import { Container, Row, Col, Card, Table } from 'react-bootstrap';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";

import sampleFlagImage from '../../assets/images/sample-flag.png';
import MetroModal from './MetroModal';
import CustomTable from '../../components/customTable/CustomTable';
//Charts
import SparklineCustom from '../../components/chart/SparklineCustom';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import MapChart from '../../components/chart/MapChart';
import Legend from '../../components/legend/Legend';
import LineChart from '../../components/chart/LineChart';
import CustomerModal from './CustomerModal';


const CustomerContent = (props) => {
  const { customerData, modalShow, modalData, setModalShow, setModalData, setReferenceID } = props;
  const top_ten_metros_by_mrr_stacked_column = customerData.top_ten_metros_by_mrr_stacked_column;
  const top_ten_metros_by_mrr_line = customerData.top_ten_metros_by_mrr_line;
  const deployment_map = customerData.deployment_map;
  const customer_ranking = customerData.customer_ranking;
  const by_rank_id = customer_ranking.by_rank_id;


  const indicatorUp = (cell, row) => {
    return (
      <span className="indicator indicator-up">
        <ImArrowUpRight2 />
        {cell}
      </span>
    );
  }

  const indicatorDown = (cell, row) => {
    return (
      <span className="indicator indicator-down">
        <ImArrowDownRight2 />
        {cell}
      </span>
    );
  }

  const sparkline = (cell, row) => {
    return (
      <SparklineCustom />

    );
  }


  // For Ranks ===
  const rankFormat = (cell, row, rowIndex, formatExtraData) => {
    return (
      <span>{rowIndex + 1}</span>
    );
  }
  // For Ranks ===
  // For Selecting Row ===
  const handleOnSelect = (row, isSelect, rowIndex) => {
    var modalDataByRank;
    for (const key in by_rank_id) {
      const element = by_rank_id[key];
      if (element._id === row._id) {
        modalDataByRank = element
      }
    }
    if (isSelect) {
      setModalShow(true);
      setReferenceID(row._id);
      setModalData(modalDataByRank);
    }
  }

  const selectRow = {
    mode: 'radio',
    clickToSelect: true,
    hideSelectColumn: true,
    onSelect: handleOnSelect,
    classes: 'selected-row',
    bgColor: '#ecf2ff'
  };

  const rowClasses = () => {
    let classes = 'selectable';
    return classes
  }
  // For Selecting Row ===

  // For Initial Sort ===
  const defaultSorted = [
    {
      dataField: 'segment',
      order: 'desc', // or asc
    }
  ];
  // For Initial Sort ===

  const columns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: '', // For Ranks
      text: 'Rank',
      align: 'center',
      headerAlign: 'center',
      classes: 'rank',
      headerClasses: 'rank',
      formatter: rankFormat,
    },
    {
      dataField: 'customer',
      text: 'CUSTOMER',
      align: 'left',
      headerAlign: 'left',
      classes: 'customer',
      headerClasses: 'customer',
    },
    {
      dataField: 'mrr',
      text: 'MRR',
      sort: true,
      align: 'right',
      headerAlign: 'right'
    },
    {
      dataField: 'mrr_mix_percent',
      text: 'MRR MIX%',
      sort: true,
      align: 'center',
      headerAlign: 'center'
    },
    {
      dataField: '',
      text: '',
      align: 'center',
      headerAlign: 'center',
      classes: 'sparkline-td',
      formatter: sparkline
    },
    {
      dataField: 'y_by_y_growth_percent',
      text: 'Y/Y GROWTH%',
      sort: true,
      formatter: indicatorUp,
      align: 'center',
      headerAlign: 'center'
    },
    {
      dataField: 'y_to_y_usd_growth',
      text: 'Y-Y $GROWTH',
      sort: true,

      align: 'center',
      headerAlign: 'center'
    },
    {
      dataField: 'percent_total_usd_growth',
      text: '% TOTAL $ GROWTH',
      sort: true,
      align: 'center',
      headerAlign: 'center'
    }
  ]
  return (
    <Container fluid>
      <CustomerModal
        show={modalShow}
        onHide={() => setModalShow(false)}
        data={modalData}
      />
      <Row className="contentDivider">
        <Col md={6}>
          <Card className="revenue-card">
            <Card.Body>
              <Card.Title>Top 10 Customers by MRR</Card.Title>
              <Legend legend={top_ten_metros_by_mrr_stacked_column.legend} />
              <div style={{ height: '250px' }}>
                <StackedColumnChart
                  id="top_ten_metros_by_mrr_stacked_column"
                  isOneHundredPercent={true}              
                  isPercentage={true}
                  hiddenValueAxis={true}
                  data={top_ten_metros_by_mrr_stacked_column.data}
                  legend={top_ten_metros_by_mrr_stacked_column.legend}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col md={6}>
          <Card className="revenue-card">
            <Card.Body>
              <Card.Title>Top 10 Customers by Y/Y Growth</Card.Title>
              <Legend legend={top_ten_metros_by_mrr_line.legend} />
              <div style={{ height: "250px" }}>
                <LineChart
                  id={"total_revenue_overview_line_chart"}
                  data={top_ten_metros_by_mrr_line.data}
                  legend={top_ten_metros_by_mrr_line.legend}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row className="contentDivider" style={{ paddingTop: "30px" }}>
        <Col md={6}>
          <Card className="revenue-card">
            <Card.Body>
              <Card.Title>deployment map</Card.Title>
              <Legend legend={deployment_map.legend} />
              <div style={{ height: "420px ", paddingTop: "2rem" }}>
                <MapChart
                  id={"customer_revenue_map_chart"}
                  largerWidth={true}
                  data={deployment_map.data}
                />
              </div>
            </Card.Body>
          </Card>

        </Col>
        <Col md={6}>
          <Card className="revenue-card">
            <Card.Body>
              <Card.Title>customer ranking</Card.Title>
              <CustomTable
                tableName='customer-ranking scrollable'
                data={customer_ranking.by_rank.data === undefined ? [] : customer_ranking.by_rank.data}
                selectRow={selectRow} // For Selecting Row ===
                defaultSorted={defaultSorted} // For Initial Sort ===
                rowClasses={rowClasses}
                columns={columns}
              />
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </Container>

  );
};

export default CustomerContent;