import React, { useState, forwardRef, Children } from 'react';
import { Dropdown, Form, Button, FormControl } from 'react-bootstrap';
import { BiChevronDown } from 'react-icons/bi'
import './customDropdown.scss';


const SearchCheckboxDD = ({ options, className, id, title, visibleInModal }) => {
  const [value, setValue] = useState([]);
  const [show, setShow] = useState(false);
  const [searchVal, setSearchVal] = useState('');

  const handleSelect = (e) => {
    const checkValue = value.some(item => item === e);
    const remove = value.filter(item => item !== e);
    if (!checkValue) {
      setValue([...value, e])
    } else {
      setValue(remove)
    }
  }

  const handleToggle = () => {
    setShow(!show)
  }

  const handleCancel = () => {
    setShow(!show)
  }

  const CustomToggle = forwardRef(({ children, onClick }, ref) => (
    <Button
      className="dropdown-toggle"
      variant='light'
      ref={ref}
      onClick={(e) => {
        e.preventDefault();
        onClick(e);
      }}>
      {children}
    </Button>
  ));

  const CustomMenu = forwardRef(
    ({ children, style, className, 'aria-labelledby': labeledBy }, ref) => {

      return (
        <div
          ref={ref}
          style={style}
          className={className}
          aria-labelledby={labeledBy}
        >
          <FormControl
            autoFocus
            className="my-2 w-auto"
            placeholder={`Search for ${title}`}
            onChange={(e) => setSearchVal(e.target.value)}
            value={searchVal}
          />
          <ul className="list-of-checkbox custom-scroll">
            {Children.toArray(children).filter(
              (child) => !searchVal || child.props.label.toLowerCase().includes(searchVal)
            )}
          </ul>
          <div className="btn-block">
            <Button variant="link" onClick={handleCancel}>Cancel</Button>
            <Button variant="primary">Apply</Button>
          </div>
        </div>
      );
    },
  );
console.log('visible:',visibleInModal);
  return (
    <>
      {visibleInModal
        ? <div className="checkbox-in-modal narrow-list filter-modal">
          <div className="title">{title}</div>
          <div>
            <FormControl
              className="mb-2"
              placeholder={`Search for ${title}`}
              onChange={(e) => setSearchVal(e.target.value)}
              value={searchVal}
            />
            <ul className="list-of-checkbox custom-scroll">
              <Form>
                {options.map(option => (
                  <Form.Check
                    key={option.key}
                    custom
                    type='checkbox'
                    id={`check_modal-${option.key}`}
                    label={option.text}
                    checked={value.some(item => item === option.key)}
                    onChange={() => handleSelect(option.key)}
                  />
                )).filter(
                  (child) => !searchVal || child.props.label.toLowerCase().includes(searchVal)
                )}
              </Form>
            </ul>
          </div>
        </div>
        : <Dropdown
          show={show}
          onToggle={handleToggle}
          className={`search-checkbox-dd narrow-list ${className ? className : ''}`}
        >
          <Dropdown.Toggle as={CustomToggle} id={`search_checkbox_dd-${id}`}>
            {title} <BiChevronDown viewBox="6 8 12 12" />
          </Dropdown.Toggle>

          <Dropdown.Menu show={show} as={CustomMenu}>
            {options.map(option => (
              <Form.Check
                key={option.key}
                custom
                type='checkbox'
                id={`check-${option.key}`}
                label={option.text}
                checked={value.some(item => item === option.key)}
                onChange={() => handleSelect(option.key)}
              />
            ))}
          </Dropdown.Menu>
        </Dropdown>
      }
    </>
  );
}

export default SearchCheckboxDD;
