import React from 'react';
import { Modal, Button } from 'react-bootstrap';

import RadioDD from '../../components/customDropdown/RadioDD';
import SearchCheckboxDD from '../../components/customDropdown/SearchCheckboxDD';

const AddMetricsModal = (props) => {
  const data = props.data;
  const setContent= props.setContent;

  return (
    <Modal id="metrics-modal" className="metrics-modal"
      {...props}
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header>
        <Modal.Title id="contained-modal-title-vcenter">
          choose metrics to add
        </Modal.Title>
      </Modal.Header>
      <Modal.Body className="custom-modal">
        <RadioDD
          title="Metric Category"
          id="metric-category"
          options={[
            {
              key: 'topline',
              text: 'Topline'
            },
            {
              key: 'platform',
              text: 'Platform'
            },
            {
              key: 'market',
              text: 'Market'
            },
            {
              key: 'customer',
              text: 'Customer'
            },
            {
              key: 'product',
              text: 'Product'
            },
            {
              key: 'operation',
              text: 'Operation'
            }
          ]}
        />
        <SearchCheckboxDD
          title="Metro"
          id="metro"
          visibleInModal
          options={[
            {
              key: 'total_revenue',
              text: 'Total Revenue'
            },
            {
              key: 'customer_revenue',
              text: 'Customer Revenue'
            },
            {
              key: 'gross-bookings',
              text: 'Gross Bookings'
            },
            {
              key: 'net-bookings',
              text: 'Net Bookings'
            },
            {
              key: 'cross-border-trade',
              text: 'Cross Border Trade'
            },
            {
              key: 'pricing',
              text: 'Pricing'
            },
            {
              key: 'churn',
              text: 'Churn'
            },
            {
              key: 'new-logos',
              text: 'New Logos'
            },
            {
              key: 'logo-extension',
              text: 'Logo Extension'
            },
            {
              key: 'sales-productivity',
              text: 'Sales Productivity'
            },
            {
              key: 'ebitda',
              text: 'EBITDA'
            }
          ]}
        />

      </Modal.Body>
      <div className="btn-container">
        <Button variant="link" onClick='' size="sm">Cancel</Button>
        <Button variant="primary" size="sm" onClick={() => {
          setContent(true)
        }}>Next</Button>
      </div>

    </Modal>
  );
}

export default AddMetricsModal;