import React from 'react';
import { useHistory, Link } from 'react-router-dom';
import { Form, Button, Alert } from 'react-bootstrap';
import { ReactComponent as Logo } from '../../assets/images/orbis-logo-dark.svg';
import Video from '../../assets/videos/main-visual.mp4';
import { BiChevronDown } from 'react-icons/bi';
import { VscLoading } from "react-icons/vsc";

import './login.scss';
import { useState } from 'react';

const Login = () => {
  const [username, setUsername] = useState('');
  const [isError, setError] = useState(false);
  const [isLoading, setLoading] = useState(false);
  
  const onChange = (e) => {
    setUsername(e.target.value)
  }

  let history = useHistory();

  const onKeyUp = () => {
    setError(false)
  }

  const onSubmit = (e) => {
    e.preventDefault();
    setLoading(true)
    setTimeout(() => {
      if( username === 'gleng@equinix.com'){
        localStorage.setItem("username", username);
        history.push("/sso-equinix-com");
        setLoading(false)
      } else {
        setError(true)
        setLoading(false)
      }
    }, 2000)
  }

  return (
    <div className="login-page">
      <section className="section-1">
        <div className="video-container">
          <video autoPlay muted loop className="video" id="mainVisual">
            <source src={Video} type="video/mp4" />
              Your browser does not support HTML5 video.
            </video>
        </div>
        <div className="section-wrapper">
          <div className="login-block">
            <div className="login-container">
              <div className="logo">
                <Logo width="277px" />
              </div>
              <p>Welcome back! Please login to your account.</p>
              <Form onSubmit={onSubmit}>
                <Form.Group controlId="username">
                  {isError && <Alert variant="danger">We cant find your account or your Username is incorrect.</Alert>}
                  <Form.Control 
                    type="text" 
                    placeholder="Username" 
                    name="username" 
                    onKeyUp={onKeyUp}
                    value={username} 
                    required 
                    onChange={onChange} 
                  />
                </Form.Group>
                <div className="btn-block">
                  <Button 
                    type="submit" 
                    className="btn-login"
                    disabled={isLoading ? true : false}
                  >
                    Login 
                    {isLoading && <VscLoading className="is-loading"/>}
                  </Button>
                  <Button variant="light" className="btn-request">Request Access</Button>
                </div>
                <Link to="/login" className="terms">Term of use. Privacy policy</Link>
              </Form>
            </div>
            <Button variant="link" className="btn-scroll"><BiChevronDown viewBox="7 8 10 10" className="icon" /> Scroll Down to Learn More</Button>
          </div>
        </div>
      </section>
    </div>
  );
};

export default Login;
