import React from 'react';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";
import { Modal, Row, Col, Table } from 'react-bootstrap';
import SparklineCustom from '../../components/chart/SparklineCustom';
import MapModalChart from '../../components/chart/MapModalChart';
import DonutChart from '../../components/chart/DonutChart';
import Kpi from '../../components/kpi/Kpi';
import Legend from '../../components/legend/Legend';
import sampleFlagImage from '../../assets/images/sample-flag.png';

import CustomTable from '../../components/customTable/CustomTable';

import './customerRevenue.scss';

const CustomerModal = (props) => {
  const data = props.data;
  console.log('customer-modal: ', data);
  var overall,
    vertical_details,
    vertical_revenue_mix,
    customer, deployment_map;
  if (data) {
    overall = data.overall;
    vertical_details = data.vertical_details;
    vertical_revenue_mix = data.vertical_revenue_mix;
    deployment_map = data.deployment_map
    customer= data.customer;
  }


  const indicatorUp = (cell, row) => {
    return (
      <span className="indicator indicator-up">
        <ImArrowUpRight2 />
        {cell}
      </span>
    );
  }

  const indicatorDown = (cell, row) => {
    return (
      <span className="indicator indicator-down">
        <ImArrowDownRight2 />
        {cell}
      </span>
    );
  }

  const sparkline = (cell, row) => {
    return (
      <SparklineCustom />

    );
  }
  const metroColumn = (cell, row) => {
    return (
      <>
        <img className="flag-icon" src={sampleFlagImage} alt="sample flag" />
        <span className="body-name">{cell}</span>
      </>

    )
  }

  // For Ranks ===
  const rankFormat = (cell, row, rowIndex, formatExtraData) => {
    return (
      <span>{rowIndex + 1}</span>
    );
  }
  // For Ranks ===

  const rowClasses = () => {
    let classes = 'selectable';
    return classes
  }
  // For Selecting Row ===

  // For Initial Sort ===
  const defaultSorted = [
    {
      dataField: 'segment',
      order: 'desc', // or asc
    }
  ];
  // For Initial Sort ===

  const columns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: '', // For Ranks
      text: 'Rank',
      align: 'center',
      headerAlign: 'center',
      classes: 'rank',
      headerClasses: 'rank',
      formatter: rankFormat,
    },
    {
      dataField: 'metro',
      text: 'METRO',
      align: 'left',
      headerAlign: 'left',
      classes: 'metro',
      headerClasses: 'metro',
      formatter: metroColumn
    },
    {
      dataField: 'mrr',
      text: 'MRR',
      sort: true,
      align: 'right',
      headerAlign: 'right'
    },
    {
      dataField: 'mrr_mix_percent',
      text: 'MRR MIX%',
      sort: true,
      align: 'center',
      headerAlign: 'center'
    },
    {
      dataField: '',
      text: '',
      align: 'center',
      headerAlign: 'center',
      classes: 'sparkline-td',
      formatter: sparkline
    },
    {
      dataField: 'y_by_y_growth_percent',
      text: 'Y/Y GROWTH%',
      sort: true,
      formatter: indicatorUp,
      align: 'center',
      headerAlign: 'center'
    },
    {
      dataField: 'y_to_y_usd_growth',
      text: 'Y-Y $GROWTH',
      sort: true,

      align: 'center',
      headerAlign: 'center'
    },
    {
      dataField: 'percent_customer_total',
      text: '% CUSTOMER TOTAL',
      sort: true,
      align: 'center',
      headerAlign: 'center'
    },
    {
      dataField: 'percent_metro_total',
      text: '% METRO TOTAL',
      sort: true,
      align: 'center',
      headerAlign: 'center'
    }
  ]
  return (
    <Modal id="customer-modal" className="customer-modal"
      {...props}
      // size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          {customer}
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        {data ?
          <>
            <Row>
              <Col className="fix-width" style={{ paddingRight: "30px" }}>
                <Kpi data={overall} />
              </Col>
              <Col md={6} xs={12}>
                <Legend legend={deployment_map.legend} />
                <div style={{ height: "350px ", paddingTop: "2rem" }}>
                  <MapModalChart
                    id={"customer_revenue_map_chart_modal"}
                    largerWidth={false}
                    data={deployment_map.data}
                  />
                </div>
              </Col>
              <Col style={{ paddingLeft: "30px" }}>
                <div style={{ height: "350px" }}>
                  <DonutChart
                    id={"customer_revenue_metro_modal_donut_chart"}
                    totalTitle={"TOTAL REVENUE"}
                    total={vertical_revenue_mix.customer_revenue}
                    data={vertical_revenue_mix.data}
                    legend={vertical_revenue_mix.legend} />
                </div>
              </Col>
            </Row>
            <Row>
              <Col md={12} style={{ paddingTop: '1.5rem' }}>
                <CustomTable
                  tableName='vertical-details scrollable'
                  data={vertical_details === undefined ? [] : vertical_details}
                  defaultSorted={defaultSorted} // For Initial Sort ===
                  columns={columns}
                />
              </Col>
            </Row>
          </>
          : <div>No data available.</div>
        }
      </Modal.Body>
    </Modal>
  );
}

export default CustomerModal;