import React from 'react';
import { Container, Row, Col, Card, Table } from 'react-bootstrap';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";

import sampleFlagImage from '../../assets/images/sample-flag.png';
import MetroModal from './MetroModal';
import CustomTable from '../../components/customTable/CustomTable';
//Charts
import SparklineCustom from '../../components/chart/SparklineCustom';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import StackedColumnBar from '../../components/chart/StackedColumnBar';
import MapChart from '../../components/chart/MapChart';
import Legend from '../../components/legend/Legend';


const MetroContent = (props) => {
  const { metroData, modalShow, modalData, setModalShow, setModalData, setReferenceID } = props;
  const metro_ranking = metroData.metro_ranking;
  const metro_number_by_customer_revenue_size = metroData.metro_number_by_mrr_size;
  const metro_number_by_y_by_growth = metroData.metro_number_by_y_by_growth;
  const metro_number_by_y_to_y_usd_growth = metroData.metro_number_by_y_to_y_usd_growth;
  const top_ten_metros_by_total_revenue = metroData.top_ten_metros_by_mrr;
  const by_rank_id = metro_ranking.by_rank_id;

  const indicatorUp = (cell, row) => {
    return (
      <span className="indicator indicator-up">
        <ImArrowUpRight2 />
        {cell}
      </span>
    );
  }

  const indicatorDown = (cell, row) => {
    return (
      <span className="indicator indicator-down">
        <ImArrowDownRight2 />
        {cell}
      </span>
    );
  }

  const sparkline = (cell, row) => {
    return (
      <SparklineCustom />

    );
  }

  const barchart = (cell, row) => {
    return (
      <StackedColumnBar data={cell} />
    )
  }

  const metro = (cell, row) => {
    return (
      <>
        <img className="flag-icon" src={sampleFlagImage} alt="sample flag" />
        <span className="body-name">{cell}</span>
      </>

    )
  }
  // For Ranks ===
  const rankFormat = (cell, row, rowIndex, formatExtraData) => {
    return (
      <span>{rowIndex + 1}</span>
    );
  }
  // For Ranks ===
  // For Selecting Row ===
  const handleOnSelect = (row, isSelect, rowIndex) => {
    var modalDataByRank;
    for (const key in by_rank_id) {
      const element = by_rank_id[key];
      if (element._id === row._id) {
        modalDataByRank = element
      }
    }
    if (isSelect) {
      setModalShow(true);
      setReferenceID(row._id);
      setModalData(modalDataByRank);
    }
  }

  const selectRow = {
    mode: 'radio',
    clickToSelect: true,
    hideSelectColumn: true,
    onSelect: handleOnSelect,
    classes: 'selected-row',
    bgColor: '#ecf2ff'
  };

  const rowClasses = () => {
    let classes = 'selectable';
    return classes
  }
  // For Selecting Row ===

  // For Initial Sort ===
  const defaultSorted = [
    {
      dataField: 'segment',
      order: 'desc', // or asc
    }
  ];
  // For Initial Sort ===

  const columns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: '', // For Ranks
      text: 'Rank',
      align: 'center',
      headerAlign: 'center',
      classes: 'rank',
      headerClasses: 'rank',
      formatter: rankFormat,
    },
    {
      dataField: 'metro',
      text: 'METRO',
      align: 'left',
      headerAlign: 'left',
      classes: 'segment',
      headerClasses: 'segment',
      formatter: metro
    }, {
      dataField: 'stacked_column_bar_data',
      text: '',
      align: 'center',
      headerAlign: 'center',
      classes: 'sparkline-td',
      formatter: barchart
    },
    {
      dataField: 'mrr',
      text: 'MRR',
      sort: true,
      align: 'right',
      headerAlign: 'right'
    },

    {
      dataField: 'mix_percent',
      text: 'MIX PERCENT',
      sort: true,
      align: 'center',
      headerAlign: 'center'
    },

    {
      dataField: 'y_by_y_growth_percent',
      text: 'Y/Y GROWTH%',
      sort: true,
      formatter: indicatorUp,
      align: 'center',
      headerAlign: 'center'
    },
    {
      dataField: 'y_to_y_usd_growth',
      text: 'Y-Y $GROWTH',
      sort: true,

      align: 'center',
      headerAlign: 'center'
    },
    {
      dataField: 'percent_total_usd_growth',
      text: '% TOTAL $ GROWTH',
      sort: true,
      align: 'center',
      headerAlign: 'center'
    }
  ]
  return (
    <Container fluid>
      <MetroModal
        show={modalShow}
        onHide={() => setModalShow(false)}
        data={modalData}
      />
      <Row className="contentDivider">
        <Col md={3} xs={12}>
          <Card className="revenue-card">
            <Card.Body>
              <Card.Title>top 10 metros by total rev.</Card.Title>
              <Legend legend={top_ten_metros_by_total_revenue.legend} />
              <div style={{ height: '250px' }}>
                <StackedColumnChart
                  id="customer_revenue_top_ten_metros_by_customer_revenue_stacked_column_chart"
                  isOneHundredPercent={true}
                  isPercentage={true}
                  hiddenValueAxis={true}
                  data={top_ten_metros_by_total_revenue.data}
                  legend={top_ten_metros_by_total_revenue.legend}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col md={3} xs={12}>
          <Card className="revenue-card">
            <Card.Body>
              <Card.Title>Metro # By Total rev size</Card.Title>
              <Legend legend={metro_number_by_customer_revenue_size.legend} />
              <div style={{ height: "250px" }}>
                <StackedColumnChart
                  id="customer_revenue_metro_number_by_customer_revenue_size"
                  largerWidth={true}
                  hiddenValueAxis={true}
                  isOneHundredPercent={false}
                  isPercentage={false}
                  data={metro_number_by_customer_revenue_size.data}
                  legend={metro_number_by_customer_revenue_size.legend}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col md={3} xs={12}>
          <Card className="revenue-card">
            <Card.Body>
              <Card.Title>Metro # By Y/Y growth</Card.Title>
              <Legend legend={metro_number_by_y_by_growth.legend} />
              <div style={{ height: "250px" }}>
                <StackedColumnChart
                  id="customer_revenue_metro_number_by_y_by_growth"
                  largerWidth={true}
                  hiddenValueAxis={true}
                  isOneHundredPercent={false}
                  isPercentage={false}
                  data={metro_number_by_y_by_growth.data}
                  legend={metro_number_by_y_by_growth.legend}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col md={3} xs={12}>
          <Card className="revenue-card">
            <Card.Body>
              <Card.Title>Metro # By Y-Y $Growth</Card.Title>
              <Legend legend={metro_number_by_y_to_y_usd_growth.legend} />
              <div style={{ height: "250px" }}>
                <StackedColumnChart
                  id="customer_revenue_metro_number_by_y_to_y_usd_growth"
                  largerWidth={true}
                  hiddenValueAxis={true}
                  isOneHundredPercent={false}
                  isPercentage={false}
                  data={metro_number_by_y_to_y_usd_growth.data}
                  legend={metro_number_by_y_to_y_usd_growth.legend}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row className="contentDivider" style={{ paddingTop: "30px" }}>
        <Col md={6}>
          <Card className="revenue-card">
            <Card.Body>
              <Card.Title>deployment map</Card.Title>
              <Legend legend={metroData.deployment_map.legend} />
              <div style={{ height: "420px ", paddingTop: "2rem" }}>
                <MapChart
                  id={"customer_revenue_map_chart"}
                  largerWidth={true}
                  data={metroData.deployment_map.data}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col md={6}>
          <Card className="revenue-card">
            <Card.Body>
              <Card.Title>metro ranking</Card.Title>
              <Legend legend={metro_ranking.by_rank.legend} />
              <div className="pb-5"></div>
              <CustomTable
                tableName='metro-ranking scrollable'
                data={metro_ranking.by_rank.data === undefined ? [] : metro_ranking.by_rank.data}
                selectRow={selectRow} // For Selecting Row ===
                defaultSorted={defaultSorted} // For Initial Sort ===
                rowClasses={rowClasses}
                columns={columns}
              />
              <Table responsive className="separate-table">
                <tbody>
                  {
                    metro_ranking.by_top_rank.map((data, i) => {
                      return (
                        <tr key={i} className={i === 0 ? 'border-head height1' : 'height1'}>
                          <td className="rank"></td>
                          <td>{data.metro}</td>
                          <td className="sparkline-td">
                            <StackedColumnBar data={data.stacked_column_bar_data} />
                          </td>
                          <td className="text-center">{data.mrr}</td>
                          <td style={{ paddingLeft: '2rem' }} >{data.mix_percent}</td>

                          <td style={{ paddingLeft: '1.2rem' }}>
                            <span className="indicator indicator-up " style={{ top: "70.7%" }}>
                              <ImArrowUpRight2 />
                              {data.y_by_y_growth_percent}
                            </span></td>
                          <td style={{ paddingLeft: '1.75rem' }}>{data.y_to_y_usd_growth}</td>
                          <td style={{ paddingLeft: '2.2rem' }}>{data.mix_percent}</td>
                        </tr>
                      )
                    })
                  }
                </tbody>
              </Table>
            </Card.Body>
          </Card>
        </Col>
      </Row>

    </Container>
  );
};

export default MetroContent;