import React, { useState, useContext, useEffect } from 'react';
import axios from 'axios';
import qs from 'query-string';
import { useHistory } from 'react-router-dom';
import { Form, Button, Alert } from 'react-bootstrap';
import Authenticated from '../../assets/images/phone-correct.png';
import FooterLogo from '../../assets/images/ping-id.png';
import { VscLoading } from "react-icons/vsc";
import { API_BASE } from '../../constants/config';
import { AuthContext } from '../../context/AuthContext';

import './fakeLogin.scss';

const initialState = {
  grant_type: 'password',
  username: '',
  password: '',
}

const FakeLogin = () => {
  let history = useHistory();
  const { dispatch } = useContext(AuthContext);

  const [sms_code, setCode] = useState('');
  const [appear, setAppear] = useState(false);
  const [openSms, setOpen] = useState(false);
  const [isError, setError] = useState(false);
  const [isLoading, setLoading] = useState(false);

  const [signOn, setSignOn] = useState(initialState);

  const onSubmit = () => {
    setAppear(true)
    setTimeout(() => {
      history.push("/");
    }, 3000)
  }

  useEffect(() => {
    setSignOn({
      ...signOn,
      username: localStorage.getItem('username')
    }) 
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  const onChange = (e) => {
    setSignOn({
      ...signOn, 
      [e.target.name]: e.target.value
    })
  }

  const goToSms = (e) => {
    e.preventDefault();
    setLoading(true);

    axios({
			method: 'post',
			url: `${API_BASE}/orbis/token`,
			data: qs.stringify(signOn),
			headers: {
        'Content-Type': 'application/x-www-form-urlencoded'
			},
		}).then(result => {
      console.log(result.data)
        dispatch({
          type: "LOGIN",
          payload: result.data
        })
        setTimeout(() => {
          setOpen(true);
          setLoading(false)
        }, 2000)
		}).catch(error => {
      console.log(error)
      setError(true)
			setLoading(false);
		})
  }

  return (
    <>
      {openSms
        ? <FakeSMS
          appear={appear}
          onSubmit={onSubmit}
          sms_code={sms_code}
          setCode={setCode}
        />
        : <div className="fake-login">
          <div className="container-area">
            <h4>Sign On</h4>
            <Form onSubmit={goToSms} autoComplete="false">
              {isError && <Alert variant="danger">Incorrect password.</Alert>}
              <Form.Group controlId="username" className="mb-4">
                <Form.Label>Username</Form.Label>
                <input type="username" name="username" className="d-none"/>
                <Form.Control 
                  type="text" 
                  placeholder="Username" 
                  name="username" 
                  required 
                  value={signOn.username} 
                  onChange={onChange}
                />
              </Form.Group>
              <Form.Group controlId="password">
                <Form.Label>Password</Form.Label>
                <input type="password" name="password" className="d-none"/>
                <Form.Control 
                  type="password" 
                  placeholder="Password" 
                  name="password" 
                  required 
                  value={signOn.password} 
                  onChange={onChange}
                />
              </Form.Group>
              <div className="btn-block">
                <Button 
                  type="submit" 
                  variant="info"
                  disabled={isLoading ? true : false}
                >
                  Sign On
                  {isLoading && <VscLoading className="is-loading"/>}
                </Button>
              </div>
            </Form>
          </div>

          <div className="footer-area">
            <p className="powered">Powered by PingIdentity</p>
            <p className="copy">Copyright &copy; 2003-2019. Ping Identity Corporation. All rights reserved.</p>
          </div>
        </div>
      }
    </>
  );
};

const FakeSMS = ({ appear, onSubmit, sms_code, setCode }) => {
  return (
    <div className="fake-sms">
      <div className="container-area">
        {appear
          ? <div className="container-wrapper green">
            <p><img src={Authenticated} alt="" /></p>
            <h4>Authenticated</h4>
          </div>
          : <div className="container-wrapper">
            <h6>Authentication</h6>
            <p className="text-bold">SMS sent to Mobile 1(********91)</p>
            <p className="mb-5">Enter the passcode you received.</p>

            <Form onSubmit={onSubmit}>
              <Form.Control type="text" name="sms_code" value={sms_code} onChange={(e) => setCode(e.target.value)} />
              <Button variant="resend">Resend Passcode</Button>
              <div className="btn-block">
                <Button variant="outline-light">Change Device</Button>
                <Button variant="outline-light"
                  disabled={sms_code === '' ? true : false}
                  onClick={() => onSubmit()}
                >
                  Sign On
                </Button>
              </div>
            </Form>
          </div>
        }
        <p className="help">Please contact help@equinix.com</p>
        <Button variant="outline-secondary">Settings</Button>
      </div>

      <div className="footer-area">
        <p className="logo"><img src={FooterLogo} alt="" /></p>
        <p className="copy">Copyright &copy; 2003-2019. Ping Identity Corporation. All rights reserved.</p>
      </div>
    </div>
  );
}

export default FakeLogin;
