import React, { useState } from 'react';
import { Dropdown, Form, Button } from 'react-bootstrap';
import { BiChevronDown } from 'react-icons/bi'
import './customDropdown.scss';


const RadioDD = ({ options, className, id, title, visibleInModal }) => {
  const initialValue = options ? options[0].text : ''
  const [value, setValue] = useState(initialValue);
  const [show, setShow] = useState(false);

  const handleSelect = (e) => {
    setValue(e)
  }

  const handleToggle = () => {
    setShow(!show)
  }

  const handleCancel = () => {
    setShow(!show)
  }

  return (
    <>
      {visibleInModal
        ? <div className="radio-in-modal narrow-list filter-modal">
          <div className="title">{title ? title : value}</div>
          <Form>
            {options && options.map(option => (
              <Form.Check
                key={option.key}
                custom
                type='radio'
                id={`modal_radio-${option.key}`}
                checked={value === option.text}
                label={option.text}
                onChange={() => handleSelect(option.text)}
              />
            ))}
          </Form>
        </div>
        : <Dropdown
          show={show}
          onToggle={handleToggle}
          className={`radio-dd narrow-list ${className ? className : ''}`}
        >
          <Dropdown.Toggle id={`radio_dd-${id}`} variant='light'>
            {title} <BiChevronDown viewBox="6 8 12 12" />
          </Dropdown.Toggle>

          <Dropdown.Menu show={show}>
            {options && options.map(option => (
              <Form.Check
                key={option.key}
                custom
                type='radio'
                id={`raido-${option.key}`}
                checked={value === option.text}
                label={option.text}
                onChange={() => handleSelect(option.text)}
              />
            ))}
            <div className="btn-block">
              <Button variant="link" onClick={handleCancel}>Cancel</Button>
              <Button variant="primary">Apply</Button>
            </div>
          </Dropdown.Menu>
        </Dropdown>
      }
    </>
  );
}

export default RadioDD;
