import React, { useState } from 'react';
import { Container, Row, Col, Card, Table } from 'react-bootstrap';
import { ImArrowUpRight2, ImArrowDownRight2 } from "react-icons/im";

import CustomTable from '../../components/customTable/CustomTable';
import BubbleChart from '../../components/chart/BubbleChart';
import LineChart from '../../components/chart/LineChart';
import StackedColumnChart from '../../components/chart/StackedColumnChart';
import ResponsiveBasicDD from '../../components/customDropdown/ResponsiveBasicDD';
import Legend from '../../components/legend/Legend';
import { optionGroup } from '../../constants/filterOptions';

const GrowthContent = (props) => {
  const { bubbleName, selectedData, idState, width, resultData, bubbleSelection, selectedBubbleData, setBubbleSelection, setSelectedData, setSelectedBubbleData, setBubbleName, setReferenceID, growthData } = props;
  const segment_ranking = growthData.segment_ranking;

  var by_rank_id;
  if (resultData) {
    by_rank_id = resultData.data.by_rank_id;
  }

  if (idState) {
    selectedBubbleData.map(item => {
      if (item._id !== idState) {
        item.color = 'rgba(234, 240, 244, 0.2)';
      } else {
        if (item.region === 'APAC') {
          item.color = '#CD503C'
        } else if (item.region === 'AMER') {
          item.color = '#DC7431'
        } else if (item.region === 'EMEA') {
          item.color = '#F0CC71'
        }

      }
    })

  }

  const indicatorUp = (cell, row) => {
    return (
      <span className="indicator indicator-up">
        <ImArrowUpRight2 />
        {cell}
      </span>
    );
  }

  const indicatorDown = (cell, row) => {
    return (
      <span className="indicator indicator-down">
        <ImArrowDownRight2 />
        {cell}
      </span>
    );
  }
  // For Ranks ===
  const rankFormat = (cell, row, rowIndex, formatExtraData) => {
    return (
      <span>{rowIndex + 1}</span>
    );
  }
  // For Ranks ===
  // For Selecting Row ===
  const handleOnSelect = (row, isSelect, rowIndex) => {
    var selectedData;
    for (const key in by_rank_id) {
      const element = by_rank_id[key];
      if (element._id === row._id) {
        selectedData = element
      }
    }
    if (isSelect) {
      setReferenceID(row._id);
      setSelectedData(selectedData);
      setSelectedBubbleData(growthData.mrr_size_and_growth.data)
      setBubbleName(row.segment)
    }
  }

  const selectRow = {
    mode: 'radio',
    clickToSelect: true,
    hideSelectColumn: true,
    onSelect: handleOnSelect,
    classes: 'selected-row',
    bgColor: '#ecf2ff'
  };

  const rowClasses = () => {
    let classes = 'selectable';
    return classes
  }
  // For Selecting Row ===

  // For Initial Sort ===
  const defaultSorted = [
    {
      dataField: 'segment',
      order: 'desc', // or asc
    }
  ];
  // For Initial Sort ===

  const columns = [
    {
      dataField: '_id',
      text: 'id',
      hidden: true
    },
    {
      dataField: '', // For Ranks
      text: 'Rank',
      align: 'center',
      headerAlign: 'center',
      classes: 'rank',
      headerClasses: 'rank',
      formatter: rankFormat,
    },
    {
      dataField: 'segment',
      text: 'SEGMENT',
      align: 'left',
      headerAlign: 'left',
      classes: 'segment',
      headerClasses: 'segment'
    },
    {
      dataField: 'mrr',
      text: 'TOTAL REVENUE',
      sort: true,
      align: 'right',
      headerAlign: 'right'
    },

    {
      dataField: 'y_by_y_growth_percent',
      text: 'Y/Y GROWTH%',
      sort: true,
      formatter: indicatorUp,
      align: 'center',
      headerAlign: 'center'
    },
    {
      dataField: 'y_to_y_usd_growth',
      text: 'Y-Y $ GROWTH',
      sort: true,
      align: 'center',
      headerAlign: 'center'
    },
    {
      dataField: 'q_mix_percent',
      text: 'MIX%',
      sort: true,
      align: 'center',
      headerAlign: 'center'
    },
    {
      dataField: 'q_by_q_growth_percent',
      text: 'Q/Q GROWTH%',
      sort: true,
      formatter: indicatorDown,
      align: 'center',
      headerAlign: 'center'
    },
    {
      dataField: 'q_to_q_usd_growth',
      text: 'Q-Q $ GROWTH',
      sort: true,
      align: 'center',
      headerAlign: 'center'
    },
    {
      dataField: 'q_mix_percent',
      text: 'MIX%',
      sort: true,
      align: 'center',
      headerAlign: 'center'
    }
  ]

  return (
    <Container className="overview" fluid>
      <Row className="contentDivider">
        <Col md="5" xs="12">
          <Card className="revenue">
            <Card.Body>
              <Card.Title>mrr size and growth</Card.Title>
              <div className="d-flex">
                <Legend legend={growthData.mrr_size_and_growth.legend} />
                <ResponsiveBasicDD
                  className=" ml-auto"
                  id="group"
                  addTitleOnWide
                  wide={width >= 1516 ? true : false}
                  narrow={width < 1516 ? true : false}
                  options={[
                    {
                      key: 'mrr_dollar',
                      text: 'Bubble = MRR$'
                    },
                    {
                      key: 'y-y_growth',
                      text: 'Bubble = Y-Y $ Growth'
                    }
                  ]}
                  selection={setBubbleSelection}
                />
              </div>

              <div style={{ height: "92%" }}>
                <BubbleChart
                  id={idState ? bubbleName : bubbleSelection ? bubbleSelection : "customer_revenue_bubble_chart"}
                  data={idState ? selectedBubbleData : growthData.mrr_size_and_growth.data}
                  selected={idState}
                  bubbleSelection={bubbleSelection}
                // legend={growthData.mrr_size_and_growth.legend}
                />
              </div>
            </Card.Body>
          </Card>
        </Col>
        <Col md="7" xs="12">
          <Row>
            <Col md="12" xs="12" style={{ paddingBottom: "0px" }}>
              <Card className="revenue">
                <Card.Body>
                  <Card.Title>segment ranking</Card.Title>
                  <CustomTable
                    tableName='segment-ranking scrollable'
                    data={segment_ranking.by_rank === undefined ? [] : segment_ranking.by_rank}
                    selectRow={selectRow} // For Selecting Row ===
                    defaultSorted={defaultSorted} // For Initial Sort ===
                    rowClasses={rowClasses}
                    columns={columns}
                  />
                  {/* <div className="scrollable">
                    <Table responsive >
                      <thead>
                        <tr>
                          <th className="text-center">Rank</th>
                          <th>segment</th>
                          <th className="text-right">mrr</th>
                          <th className="text-center">Y/Y growth%</th>
                          <th className="text-right">Y-Y $ growth</th>
                          <th className="text-right">mix%</th>
                          <th className="text-center">Q/Q growth%</th>
                          <th className="text-right">Q-Q $ growth</th>
                          <th className="text-right">mix%</th>
                        </tr>
                      </thead>
                      <tbody>
                        {
                          segment_ranking.by_rank && segment_ranking.by_rank.length > 0 ?
                            segment_ranking.by_rank.map((data, i) => {
                              var selectedData;
                              for (const key in by_rank_id) {
                                const element = by_rank_id[key];
                                if (element._id === data._id) {
                                  selectedData = element
                                }
                              }
                              return <tr key={i} className="height1"
                                onClick={() => {
                                  setReferenceID(data._id);
                                  setSelectedData(selectedData);
                                  setSelectedBubbleData(growthData.mrr_size_and_growth.data)
                                  setBubbleName(data.segment)
                                }}>
                                <td className="text-center">{data.rank}</td>
                                <td>{data.segment}</td>
                                <td className="text-right">{data.mrr}</td>
                                <td className="text-center">
                                  <span className="indicator indicator-up"> <ImArrowUpRight2 /> {data.y_by_y_growth_percent}</span>
                                </td>
                                <td className="text-right">{data.y_to_y_usd_growth}</td>
                                <td className="text-right">{data.q_mix_percent}</td>
                                <td className="text-center">
                                  <span className="indicator indicator-down"> <ImArrowDownRight2 /> {data.q_by_q_growth_percent}</span>
                                </td>
                                <td className="text-right"> {data.q_to_q_usd_growth}</td>
                                <td className="text-right">{data.q_mix_percent}</td>
                              </tr>
                            }) : null
                        }
                      </tbody>
                    </Table>
                  </div> */}

                </Card.Body>
              </Card>
            </Col>
            <Col style={{ paddingTop: "30px", paddingBottom: " 0px" }} md="12" xs="12">
              <Row >
                <Col md="6" xs="12" style={{ paddingBottom: "0px" }}>
                  <Card className="revenue">
                    <Card.Body>
                      <Card.Title>segment y/y growth trends</Card.Title>
                      <Legend legend={selectedData ? selectedData.segment_y_by_y_growth_trends.legend : growthData.segment_trends.legend} />
                      <div style={{ height: "350px" }}>
                        <LineChart
                          id={"customer_revenue_growth_line_chart"}
                          data={selectedData ? selectedData.segment_y_by_y_growth_trends.data : growthData.segment_trends.data}
                          legend={selectedData ? selectedData.segment_y_by_y_growth_trends.legend : growthData.segment_trends.legend}
                        />
                      </div>
                    </Card.Body>
                  </Card >
                </Col>
                <Col md="6" xs="12" style={{ paddingBottom: "0px" }}>
                  <Card className="revenue">
                    <Card.Body>
                      <Card.Title>segment by region</Card.Title>
                      <Legend legend={selectedData ? selectedData.segment_by_region.legend : growthData.segment_by_region.legend} />
                      <div style={{ height: "350px" }}>
                        <StackedColumnChart
                          id="customer_revenue_segment_by_region_stacked_column_chart"
                          largerWidth={true}
                          hiddenValueAxis={true}
                          data={selectedData ? selectedData.segment_by_region.data : growthData.segment_by_region.data}
                          legend={selectedData ? selectedData.segment_by_region.legend : growthData.segment_by_region.legend}
                        />
                      </div>
                    </Card.Body>
                  </Card>
                </Col>
              </Row>

            </Col>
          </Row>
        </Col>
      </Row>
    </Container>
  );
};

export default GrowthContent;