import React, { useState } from 'react';
import { Dropdown, Form, Button } from 'react-bootstrap';
import { BiChevronDown } from 'react-icons/bi'
import './customDropdown.scss';


const CheckboxDD = ({ options, className, id, title, visibleInModal }) => {
  const [value, setValue] = useState([]);
  const [show, setShow] = useState(false);

  const handleSelect = (e) => {
    const checkValue = value.some(item => item === e);
    const remove = value.filter(item => item !== e);
    if (!checkValue) {
      setValue([...value, e])
    } else {
      setValue(remove)
    }
  }

  const handleToggle = () => {
    setShow(!show)
  }

  const handleCancel = () => {
    setShow(!show)
  }

  return (
    <>
      {visibleInModal
        ? <div className="checkbox-in-modal narrow-list filter-modal">
          <div className="title">{title ? title : value}</div>
          <Form>
            {options && options.map(option => (
              <Form.Check
                key={option.key}
                custom
                type='checkbox'
                id={`check_modal-${option.key}`}
                label={option.text}
                checked={value.some(item => item === option.key)}
                onChange={() => handleSelect(option.key)}
              />
            ))}
          </Form>
        </div>

        : <Dropdown
          show={show}
          onToggle={handleToggle}
          className={`checkbox-dd narrow-list ${className ? className : ''}`}
        >
          <Dropdown.Toggle id={`checkbox_dd-${id}`} variant='light'>
            {title} <BiChevronDown viewBox="6 8 12 12" />
          </Dropdown.Toggle>


          <Dropdown.Menu show={show}>
            {options && options.map(option => (
              <Form.Check
                key={option.key}
                custom
                type='checkbox'
                id={`check_${option.key}`}
                label={option.text}
                checked={value.some(item => item === option.key)}
                onChange={() => handleSelect(option.key)}
              />
            ))}
            <div className="btn-block">
              <Button variant="link" onClick={handleCancel}>Cancel</Button>
              <Button variant="primary">Apply</Button>
            </div>
          </Dropdown.Menu>
        </Dropdown>
      }
    </>
  );
}

export default CheckboxDD;
