import React, { useState } from 'react';
import PageHeader from '../../components/pageHeader/PageHeader';

import Metrics from './Metrics';
import './executiveCockpit.scss';

const ExecutiveCockpit = (props) => {
  let path = props.location.pathname;
  const currentPath = path.substring(path.lastIndexOf('/') + 1);
  const [activePage, setActivePage] = useState(currentPath);

  const handleActivePage = (page) => {
    setActivePage(page)
  }

  const handleBreadcrubsPreviousLink = () => {
    setActivePage('Metrics')
  }

  const breadcrumbs = [
    {
      path: path === '/executive-cockpit/metrics' ? null : '/executive-cockpit/metrics',
      label: 'Executive cockpit'
    },
    {
      label: activePage,
    }
  ]

  const innerPage = [
    {
      path: '/executive-cockpit/metrics',
      label: 'Metrics'
    }
  ]

  return (
    <div className="executiveCockpit-page">
      <PageHeader
        title="EXECUTIVE COCKPIT"
        color="dark-orange"
        innerPage={innerPage}
        breadcrumbs={breadcrumbs}
        previousPage="/"
        handleActivePage={handleActivePage}
        handleBreadcrubsPreviousLink={handleBreadcrubsPreviousLink}
      />
      <div className="page-wrapper">
        {currentPath === 'metrics' && <Metrics />}
      </div>
    </div>
  );
}

export default ExecutiveCockpit;
