import React, { useState, useEffect, useRef } from 'react';
import axios from 'axios';

// Filter components
import { useWidowResize } from '../../constants/utilityHooks';
import { optionMeasure, optionRange, optionGroup, optionRegion, optionMetro } from '../../constants/filterOptions';
import Separator from '../../components/customDropdown/Separator'
import RadioDD from '../../components/customDropdown/RadioDD'
import CheckboxDD from '../../components/customDropdown/CheckboxDD'
import ResponsiveRadioDD from '../../components/customDropdown/ResponsiveRadioDD';
import SearchCheckboxDD from '../../components/customDropdown/SearchCheckboxDD';
import FilterContainer from '../../components/filter/FilterContainer';
import FilterModal from '../../components/filter/FilterModal';
import { comments } from '../../constants/comments';
import Comment from '../../components/comment/Comment';

import { API_BASE_URL } from '../../constants/config';
import Loader from '../../components/loader/Loader';
import GrowthContent from './GrowthContent';

const Growth = () => {
    const [growthData, setData] = useState(null);
    const [isLoading, setLoadingStatus] = useState(true);
    const [resultData, setResultData] = useState(null);
    const [selectedData, setSelectedData] = useState(null);
    const [selectedBubbleData, setSelectedBubbleData] = useState(null);
    const [idState, setReferenceID] = useState(null);
    const [bubbleName, setBubbleName] = useState(null);

    const fetchData = async () => {
        setLoadingStatus(true);

        await Promise.all([
            axios(`${API_BASE_URL}/total-revenue/growth.json?print=pretty`),
            axios(`${API_BASE_URL}/total-revenue/growth/segment_ranking.json?print=pretty`)
        ]).then(response => {
            const result = response[0].data;
            const resultSelected = response[1];

            setData(result);
            setLoadingStatus(false);
            setResultData(resultSelected);
        }).catch(error => {
            console.log(error);
            setLoadingStatus(false);
        });;

    };

    useEffect(() => {
        let isMounted = true;

        if (isMounted) fetchData();

        return () => { isMounted = false };
    }, []);

    if (idState) {
        selectedBubbleData.map(item => {
            if (item._id !== idState) {
                item.color = 'rgba(234, 240, 244, 0.2)';
            }
        })
    }

    // responsive dropdowns
    const cardRef = useRef(null);
    const { width } = useWidowResize(cardRef);

    // show all filters
    const [showModal, setShowModal] = useState(false);
    const handleCloseModal = () => {
        setShowModal(false);
    }
    const handleShowModal = () => {
        setShowModal(true)
    };

    // comment area
    const [showComment, setShowComment] = useState(false);
    const handleCloseComment = () => {
        setShowComment(false);
    }
    const handleShowComment = () => {
        setShowComment(true)
    };

    if (isLoading) return <div className="w-100" ref={cardRef}><Loader /></div>;

    return (
        <>
            <div className={`page-content ${showComment === true ? 'open' : 'closed'}`} ref={cardRef}>
                <FilterContainer
                    className={`${width}`}
                    showComment={showComment}
                    handleShowModal={handleShowModal}
                    handleShowComment={handleShowComment}
                >
                    <div className="filters">
                        <CheckboxDD
                            title="Region"
                            id="measeure"
                            options={optionRegion}
                        />
                        <SearchCheckboxDD
                            title="Metro"
                            id="metro"
                            options={optionMetro}
                        />
                        <RadioDD
                            title="Measure"
                            id="measeure"
                            options={optionMeasure}
                        />
                        <Separator height='39px' />
                        <ResponsiveRadioDD
                            title="Range by"
                            id="range2"
                            wide={width >= 1394 ? true : false}
                            narrow={width < 1394 ? true : false}
                            options={optionRange}
                        />
                        <Separator height='39px' />
                        <ResponsiveRadioDD
                            title="Group by"
                            id="group"
                            addTitleOnWide
                            wide={width >= 1516 ? true : false}
                            narrow={width < 1516 ? true : false}
                            options={optionGroup}
                        />
                    </div>

                    <FilterModal
                        title="Topline"
                        setShowModal={setShowModal}
                        showModal={showModal}
                        handleCloseModal={handleCloseModal}
                    >
                        <CheckboxDD
                            title="Region"
                            id="measeure"
                            options={optionRegion}
                            visibleInModal
                        />
                        <SearchCheckboxDD
                            title="Metro"
                            id="metro"
                            options={optionMetro}
                            visibleInModal
                        />
                        <RadioDD
                            title="Measure"
                            id="measeure"
                            options={optionMeasure}
                            visibleInModal
                        />
                        <ResponsiveRadioDD
                            title="Range by"
                            id="range2"
                            options={optionRange}
                            visibleInModal
                        />
                        <ResponsiveRadioDD
                            title="Group by"
                            id="group"
                            options={optionGroup}
                            visibleInModal
                        />
                    </FilterModal>
                </FilterContainer>
                <GrowthContent
                    bubbleName={bubbleName}
                    selectedData={selectedData}
                    idState={idState}
                    width={width}
                    resultData={resultData}
                    selectedBubbleData={selectedBubbleData}
                    setSelectedData={setSelectedData}
                    setSelectedBubbleData={setSelectedBubbleData}
                    setBubbleName={setBubbleName}
                    setReferenceID={setReferenceID}
                    growthData={growthData}
                />
            </div>
            <Comment
                data={comments}
                handleCloseComment={handleCloseComment}
                showComment={showComment}
            />
        </>
    );
};

export default Growth;