import React, { useRef, useEffect } from 'react';
import { Button, ButtonGroup, Form } from 'react-bootstrap';
import moment from 'moment';
import './comment.scss';
import Dummy from '../../assets/images/dummy-image.png';
import Owner from '../../assets/images/quincy.jpeg';
import { MentionsInput, Mention } from 'react-mentions';
import { v4 as uuidv4 } from 'uuid';

import { ReactComponent as Paperclip } from '../../assets/icons/comment_paperclip.svg';
import { ReactComponent as Bubble } from '../../assets/icons/comment_bubble.svg';
import { ReactComponent as Glasses } from '../../assets/icons/comment_glasses.svg';
import { useState } from 'react';

const users = [
  {
    id: 'kyle',
    display: 'Kyle Bunda',
  },
  {
    id: 'jaye',
    display: 'Jaye ann Tagoctoc',
  },
  {
    id: 'jem',
    display: 'Jemimah Sai',
  },
  {
    id: 'jb',
    display: 'JB Ochinang',
  },
  {
    id: 'mau',
    display: 'Maurice Lim',
  },
]

const Comment = ({ className, showComment, data, handleCloseComment }) => {
  const wrapperRef = useRef(null);

  const useOutsideAlerter = (ref) => {
    useEffect(() => {
      const handleClickOutside = (event) => {
        if (ref.current && !ref.current.contains(event.target)) {
          handleCloseComment()
        }
      }
      document.addEventListener("mousedown", handleClickOutside);
      return () => {
        document.removeEventListener("mousedown", handleClickOutside);
      };
    }, [ref]);
  }

  useOutsideAlerter(wrapperRef);

  // textarea
  const [textComment, setTextComment] = useState('');
  const [comments, setComments] = useState([]);
  let dateOfComment = moment(new Date()).fromNow(true);

  const scrollRef = useRef(null);
  const scrollToRef = (ref) => scrollRef.current.scrollIntoView();
  const executeScroll = () => scrollToRef(scrollRef);

  const textareaRef = useRef(null);
  const focusOnTextArea = () => textareaRef.current.focus();

  const transformTextToHTML = (text) => {
    let str = text.replace(/@{{/g, '<span class="tag">@').replace(/}}/g, '</span>');
    return <div dangerouslySetInnerHTML={{ __html: str }}></div>;
  }

  useEffect(() => {
    setComments(data);
  }, [data])

  const handleCommentChange = (e) => {
    setTextComment(e.target.value)
  }

  const handleCommentSubmit = async () => {
    await setComments([
      ...comments,
      {
        dateTime: dateOfComment,
        id: uuidv4(),
        img: "",
        message: textComment,
        name: "JB Ochinang",
        number_of_comments: 0,
        number_of_likes: 0,
        position: "Front-end Developer",
      }
    ])
    await setTextComment('');
    executeScroll();
  }

  return (
    <>
      <div ref={wrapperRef} className={`comment-area ${showComment ? 'open' : 'closed'} ${className ? className : ''}`}>
        <div className={`comment-content ${showComment ? 'open' : 'closed'}`}>
          <button type="button" className="close" onClick={() => handleCloseComment()}>
            <span aria-hidden="true"></span>
            <span className="sr-only">Close</span>
          </button>

          <div className="title">ASK THE ANALYST</div>
          <div className="owner">
            <div className="owner-dp">
              <img src={Owner ? Owner : Dummy} alt="" />
            </div>
            <div className="owner-details">
              <p className="name">Quincy Liao<span className="email">xliao@equinix.com</span></p>
              <p className="position">Market Strategy, Senior Manager</p>
            </div>
          </div>
          <div className="comment-list custom-scroll">
            {comments && comments.map(comment => {
              return (
                <div className="comment-block" key={comment.id}>
                  <div className="comment-details">
                    <div className="user">
                      <div className="user-dp">
                        <img src={comment.image ? comment.image : Dummy} alt={comment.name} />
                      </div>
                      <div className="user-details">
                        <p className="name">{comment.name}</p>
                        <p className="position">{comment.position} <span className="dateTime">{comment.dateTime}</span></p>
                      </div>
                    </div>
                    <div className="message">{transformTextToHTML(comment.message)}</div>
                    <div className="interactions">
                      <span>{comment.number_of_likes} Likes</span>
                      <span>{comment.number_of_comments} Comments</span>
                    </div>
                  </div>
                  <div className="commnet-footer">
                    <ButtonGroup>
                      <Button variant="link">
                        <Paperclip className="icon" /> Attachment
                      </Button>
                      <Button variant="link" onClick={() => focusOnTextArea()}>
                        <Bubble className="icon" /> Comment
                      </Button>
                      <Button variant="link">
                        <Glasses className="icon" /> View Config
                      </Button>
                    </ButtonGroup>
                  </div>
                </div>
              )
            })}
            <div ref={scrollRef}></div>
          </div>
          <div className="add-comment">
            <Form>
              <MentionsInput
                value={textComment}
                className="mention-block"
                inputRef={textareaRef}
                onChange={handleCommentChange}
              >
                <Mention
                  trigger="@"
                  className="mention"
                  displayTransform={(id, display) => `@${display}`}
                  markup='@{{__display__}}'
                  data={users}
                  renderSuggestion={(suggestion, search, highlightedDisplay) => (
                    <div className="user">{highlightedDisplay}</div>
                  )}
                />
              </MentionsInput>
              <div className="option">
                <Form.Check
                  custom
                  type='checkbox'
                  id='checkbox-attach'
                  label="Attach Screenshot"
                />
                <Button onClick={() => handleCommentSubmit()}>
                  Add Comment
                  </Button>
              </div>
            </Form>
          </div>
        </div>
      </div>
    </>
  );
}

export default Comment;
