import React, { useState } from 'react';
import { useHistory } from "react-router-dom";
import { Container, Row, Col, Button } from 'react-bootstrap';
import { motion, AnimatePresence } from 'framer-motion';


import AddMetricsModal from './AddMetricsModal';
import laptop from '../../assets/images/mochupmonitor2.png';
import person1 from '../../assets/images/person1.png';
import person2 from '../../assets/images/person2.png';
import Metro from './Metro';


import './executiveCockpit.scss';


const Metrics = ({ data }) => {
  const history = useHistory();
  const [modalShow, setModalShow] = useState(false);
  const [modalData, setModalData] = useState(null);
  const [content, setContent] = useState(false);

  return (
    <>
      {!content ?
        <div className="metrics-page">
          <AddMetricsModal
            show={modalShow}
            onHide={() => setModalShow(false)}
            data={modalData}
            setContent={setContent}
          />

          <Container fluid >
            <Row>
              <Col>
                <motion.div
                  animate={{ x: 0 }}
                  transition={{ ease: "easeOut", duration: 1.5, from: -50}}
                >
                  <div className="content align-middle">
                    <div className="title">
                      Getting the insights that are important to your business
          </div>
                    <div className="text">
                      Do you waste time organizing sticky notes, searching your email and apps for to-dos,
                      and figuring out what to work on first? Then you need one solution to prioritize your tasks,
                      manage your time, and meet your deadlines.
          </div>
                    <Button className="btn-metrics"

                      onClick={() => {
                        setModalShow(true);
                      }}>Add Metrics</Button>
                  </div>
                </motion.div>
              </Col>
              <Col>
                <div className="images">
                  <motion.img
                    animate={{ x: -50 }}
                    transition={{ ease: "easeOut", duration: 1.5}}
                    className="laptop" src={laptop} alt="sample flag" />
                  <motion.img animate={{ x: -50 }}
                    transition={{ ease: "backOut", duration: 3, from: -100}}
                    className="person-1" src={person1} alt="sample flag" />
                  <motion.img animate={{ x: -40 }}
                   transition={{ ease: "backOut", duration: 3, from: 100}}
                    className="person-2" src={person2} alt="sample flag" />
                </div>

              </Col>
            </Row>
          </Container>
        </div>

        :
        <Metro />
      }
    </>
  )
};

export default Metrics;


