import React, { useState } from 'react';
import { Switch, Route } from "react-router-dom";

import Sidebar from '../sidebar/Sidebar';
import Topbar from '../topbar/Topbar';

import Home from '../../domain/home/Home';
import ExecutiveCockpit from '../../domain/executiveCockpit/ExecutiveCockpit';
import TotalRevenue from '../../domain/totalRevenue/TotalRevenue';
import CustomerRevenue from '../../domain/customerRevenue/CustomerRevenue';
import Snapshot from '../../domain/snapshot/Snapshot';
import './dashboard.scss';


const Dashboard = () => {
  const [open, setOpen] = useState(true);

  const toggleOpen = () => {
    setOpen(!open);
  }

  return (
    <div className="dashboard">
      <Sidebar open={open} toggleOpen={toggleOpen} />
      <div className={`main-content ${open === true ? 'open' : 'closed'}`}>
        <Topbar />
        <Switch>
          <Route exact path="/" component={Home} />
          <Route path="/executive-cockpit/metrics" component={ExecutiveCockpit} />
          <Route path="/executive-cockpit/topline" component={ExecutiveCockpit} />
          <Route path="/executive-cockpit/total-revenue/overview" component={TotalRevenue} />
          <Route path="/executive-cockpit/total-revenue/growth" component={TotalRevenue} />
          <Route path="/executive-cockpit/total-revenue/metro" component={TotalRevenue} />
          <Route path="/executive-cockpit/customer-revenue/overview" component={CustomerRevenue} />
          <Route path="/executive-cockpit/customer-revenue/growth" component={CustomerRevenue} />
          <Route path="/executive-cockpit/customer-revenue/metro" component={CustomerRevenue} />
          <Route path="/executive-cockpit/customer-revenue/customer" component={CustomerRevenue} />
          <Route path="/snapshot-builder/reports" component={Snapshot} />
          <Route path="/snapshot-builder/create-new" component={Snapshot} />
          <Route path="*" component={() => "404 NOT FOUND"} />
        </Switch>
      </div>
    </div>
  );
}

export default Dashboard;
